.authentication {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0px 50px 0px 50px;
}

.auth__left {
    flex: 0.5;
    text-align: center;
}

.auth__right {
    flex: 0.5;
}

.auth__left > img {
    width: 400px;
    margin-top: 40px;
}

.auth__more{
   border: 1px solid grey;
   padding: 20px;
   margin-right: 30px;
   margin-left: 30px;
   text-align: center;
   margin-top: 15px;
}

.auth__more > span{
    font-weight: 500;
    font-size: 14px;
}

.auth__more > span > button {
    background: 0;
    color: #0095f6;
    font-weight: bolder;
    border: 0;
}

.auth__more > span > button:hover {
    cursor: pointer;
}