.login {
    display: flex;
    flex-direction: column;
    border: 1px solid grey;
    padding: 50px; 
    margin-left: 30px;
    margin-right: 30px;
}

.login > img {
    width: 270px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 25px;
}

.login > input {
    background-color: #ffffff24 ;
    border: 1px solid grey;
    border-radius: 3px;
    padding: 12px;
    margin-bottom: 20px;
    color: white;
}

.login > input:focus{
    outline: 1px solid grey;
}

.login > button {
    padding: 12px;
    border-radius: 8px;
    border: 0;
    color: white;
    background-color: #0095f6;
    font-weight: bolder;    
}

.login > button:hover {
    cursor: pointer;
}